<template>
  <div>
    <el-input v-model="filterText" placeholder="关键字模糊过滤"></el-input>
    <el-tree ref="tree" :data="treeData" :props="defaultProps"
             :filter-node-method="filterNode" default-expand-all></el-tree>
  </div>
</template>

<script>
  export default {
    name: "Tree",
    data(){
      return {
        filterText: '',
        treeData: [{
          id: 1,
          label: '一级1',
          children: [{
            id: 4,
            label: '二级1',
            children: [{
              id: 7,
              label: '三级1',
            }, {
              id: 8,
              label: '三级2',
            }, {
              id: 9,
              label: '三级3',
            }]
          }, {
            id: 5,
            label: '二级2',
            children: []
          }, {
            id: 6,
            label: '二级3',
            children: []
          }]
        }, {
          id: 2,
          label: '一级2',
          children: []
        }, {
          id: 3,
          label: '一级3',
          children: []
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    watch: {
      filterText(val){
        this.$refs.tree.filter(val)
      }
    },
    methods: {
      filterNode(value, data){
        if(!value) return true
        return data.label.indexOf(value) !== -1
      }
    }
  }
</script>